.resource-page {
	padding: 50px 0;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.text-24 {
		@include fs(24rem);
		font-weight: 500; } }

.vnl-slide {
	.slick-arrow {
		@include pos(abs, -55px 0 null null);
		@include transform(translateY(-50%));
		@include fs(0);
		&::before {
			content: '';
			@include fs(16rem);
			width: 40px;
			height: 40px;
			@include flexbox();
			align-items: center;
			align-content: center;
			justify-content: center;
			color: $white;
			@include bor-rad(5px);
			background: $blue; }
		&:hover {
			&::before {
				background: $lblue; } }
		&.slick-prev {
			right: 50px;
			&::before {
				content: '\f104'; } }
		&.slick-next {
			&::before {
				content: '\f105'; } } }
	.logo {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		height: 130px; } }
