.contact-page {

	.contact-info {
		@include pos(rel,null);
		padding: 60px 0;
		@include media-breakpoint-down(lg) {
			padding: 30px 0; }
		.bg {
			@include pos(abs, 0 null 0 0);
			right: 0;
			z-index: -1;
			&::before {
				content: '';
				display: block;
				@include pos(abs, 0 null 0 0);
				right: 0;
				background: color-rgba($black,0.5); } }
		.block-text {
			.intro-text {
				@include fs(30rem);
				line-height: 1.3;
				color: $white;
				@include pos(rel,null);
				margin-bottom: 50px;
				@include media-breakpoint-down(lg) {
					@include fs(22rem);
					margin-bottom: 30px; }
				@include media-breakpoint-down(sm) {
					margin-bottom: 15px; }
				&::before {
					content: '';
					display: inline-block;
					width: 72px;
					height: 52px;
					margin-right: 10px;
					background: url(../img/ql2.png) center center no-repeat;
					@include media-breakpoint-down(lg) {
						width: 50px;
						height: 50px;
						background-size: 100% auto; } }
				&::after {
					content: '';
					display: inline-block;
					vertical-align: bottom;
					width: 41px;
					height: 30px;
					margin-left: 10px;
					background: url(../img/qr2.png) center center no-repeat;
					@include media-breakpoint-down(lg) {
						width: 30px;
						height: 30px;
						background-size: 100% auto; } } }
			.email {
				display: inline-block;
				background: $blue;
				@include fs(28rem);
				line-height: 60px;
				padding-left: 80px;
				padding-right: 50px;
				color: $white;
				@include bor-rad(100px);
				@include pos(rel,null);
				margin-bottom: 50px;
				@include media-breakpoint-down(lg) {
					@include fs(18rem);
					line-height: 40px;
					padding-left: 50px;
					padding-right: 20px;
					margin-bottom: 30px; }
				@include media-breakpoint-down(sm) {
					margin-bottom: 15px; }
				&::before {
					content: '\f0e0';
					background: $white;
					color: $blue;
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: center;
					width: 60px;
					height: 60px;
					@include bor-rad(100px);
					@include pos(abs, 0 null null 0);
					@include media-breakpoint-down(lg) {
						width: 40px;
						height: 40px; } } }
			.phone {
				color: $white;
				line-height: 1.1;
				@include pos(rel,null);
				padding-left: 100px;
				@include media-breakpoint-down(lg) {
					padding-left: 60px; }
				@include media-breakpoint-down(sm) {
					margin-bottom: 15px; }
				&:before {
					content: '';
					display: block;
					width: 91px;
					height: 71px;
					background: url(../img/phone.png) center center no-repeat;
					@include pos(abs, 8px null null 0);
					@include media-breakpoint-down(lg) {
						width: 50px;
						height: 50px;
						background-size: 100% auto;
						top: 0; } }
				span {
					@include fs(36rem);
					display: block;
					@include media-breakpoint-down(lg) {
						@include fs(20rem); } }
				a {
					@include fs(48rem);
					display: block;
					font-weight: 700;
					@include media-breakpoint-down(lg) {
						@include fs(32rem); } } } }
		.block-form {
			.contact-form {
				background: $white;
				@include bor-rad(20px);
				overflow: hidden;
				@include media-breakpoint-down(lg) {
					@include bor-rad(5px); }
				.module-title {
					@include fs(24rem);
					text-transform: uppercase;
					font-weight: 600;
					color: $white;
					text-align: center;
					background: $blue;
					padding: 10px;
					@include media-breakpoint-down(lg) {
						@include fs(16rem); } }
				.form-wrap {
					padding: 30px;
					@include media-breakpoint-down(lg) {
						padding: 15px; } } } } }

	.contact-location {
		padding: 30px 0;
		margin-bottom: 50px;
		.location-list {
			.location {
				cursor: pointer;
				@include media-breakpoint-down(sm) {
					margin-bottom: 15px; } } }
		.location-map {
			border: 3px solid #e2e2e2;
			@include bor-rad(5px);
			@include media-breakpoint-down(md) {
				border: none;
				@include bor-rad(0); }
			// +media-breakpoint-down(sm)
			// 	display: none
			.log-map {
				display: none;
				@include media-breakpoint-down(md) {
					@include pos(fix, 50% null null 50%);
					@include transform(translate(-50%,-50%));
					z-index: 1000;
					background: $white;
					@include bor-rad(5px);
					padding: 10px;
					width: 90%;
					height: 90%;
					max-width: 700px;
					max-height: 600px;
					border: 1px solid rgba(0,0,0,.2);
					@include media-breakpoint-down(sm) {
						max-height: 400px; }
					iframe {
						width: 100% !important;
						height: 100% !important; } }

				&.active {
					display: block; }
				.close {
					background: lighten($red,10%);
					color: $white;
					text-shadow: none;
					width: 24px;
					height: 24px;
					align-items: center;
					align-content: center;
					justify-content: center;
					line-height: 1;
					@include bor-rad(100px);
					opacity: 1;
					@include pos(abs, 10px 10px null null);
					z-index: 10;
					@include fs(14rem);
					display: none;
					@include media-breakpoint-down(md) {
						@include flexbox; } } } } } }
