.bn-button {
	display: block;
	padding: 13px 40px;
	color: $white;
	@include fs(15rem);
	line-height: 20px;
	font-weight: 700;
	@include bor-rad(100px);
	text-align: center;
	text-transform: uppercase;
	@include media-breakpoint-down(lg) {
		@include fs(13rem);
		font-weight: 600;
		padding: 10px 25px; }
	@include media-breakpoint-down(sm) {
		@include fs(13rem);
		padding: 7px 20px; } }
.bt-primary {
	@extend .bn-button;
	background: $blue;
	&:hover {
		background: $lblue;
		color: $white; }
	&.sm {
		@include fs(15rem);
		padding: 10px 40px;
		@include media-breakpoint-down(sm) {
			padding: 4px 17px; } } }
.bt-outline {
	@extend .bn-button;
	padding: 10px 40px;
	border: 3px solid $blue;
	@include media-breakpoint-down(lg) {
		padding: 7px 22px; }
	@include media-breakpoint-down(sm) {
		padding: 4px 17px; }
	&:hover {
		background: $blue;
		color: $white; }
	&.sm {
		@include fs(15rem);
		border-width: 1px;
		padding: 8px 38px;
		@include media-breakpoint-down(sm) {
			padding: 2px 16px; } } }
.bt-outline2 {
	@extend .bt-outline;
	color: $blue; }

.btn-wrap {
	@include flexbox;
	align-items: center;
	align-content: center; }
