@import url('https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@400;500;600;700;800&family=Fira+Sans+Condensed:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@font-face {
	font-family: 'cooper';
	src: url(../fonts/cooperblack.ttf); }

@font-face {
	font-family: 'cooperitalic';
	src: url(../fonts/cooperblackitalic.ttf); }


body {
	font-family: 'Baloo Paaji 2', cursive;
	font-size: 16px;
	line-height: 1.5;
	color: $main;
	background: url(../img/bg.jpg) top center repeat;
	* {
		font-family: 'Baloo Paaji 2', cursive;
		line-height: inherit; } }
html {
	overflow: auto; }

hr {
	background: darken($white,10%);
	border: none; }

.fullpage {
	max-width: 1920px;
	width: 100%;
	// min-height: 100vh
	overflow: hidden;
	margin: 0 auto; }

main {
	&.small {
		font-size: 100% !important; }
	&.medium {
		font-size: 125% !important; }
	&.big {
		font-size: 150% !important; } }

.container {
	@include pos(static,unset);
	@include media-breakpoint-up(xl) {
		max-width: 1170px;
		width: 100%; }
	@include media-breakpoint-down(lg) {
		max-width: 100%;
		width: 100%; }
	@include media-breakpoint-only(lg) {
		padding: 0px 25px; } }

.container-fluid {
	@include media-breakpoint-up(xl) {
		max-width: 1400px;
		width: 100%; } }


.content {
	color: #222;
	// font-family: 'Montserrat', sans-serif
	@include fs(16rem);
	line-height: 1.3;
	@include media-breakpoint-down(lg) {
		@include fs(15rem); }
	@include media-breakpoint-down(sm) {
		@include fs(14rem);
		line-height: 1.5; }
	.row {
		margin: 0px -10px;
		.col-12 {
			padding: 0px 10px; } }
	.timenew {
		font-family: 'Times New Roman', Times, serif;
		@include fs(24rem);
		line-height: 1.3;
		font-style: italic;
		@include media-breakpoint-down(md) {
			@include fs(20rem); }
		@include media-breakpoint-down(sm) {
			@include fs(18rem); } }
	.img {
		margin-bottom: 15px; }
	.quote {
		@include fs(20rem);
		margin: 15px auto;
		padding: 20px;
		@include pos(rel,null);
		@include media-breakpoint-down(sm) {
			@include fs(16rem);
			width: 100% !important; }

		&:before {
			content: '';
			display: inline-block;
			margin-right: 10px;
			background: url(../img/ql3.png);
			width: 37px;
			height: 32px;
 }			// +pos(abs, 0 null null 0)
		&:after {
			content: '';
			display: inline-block;
			margin-left: 10px;
			vertical-align: bottom;
			width: 26px;
			height: 23px;
			background: url(../img/qr3.png);
 }			// +pos(abs, null 0 10px null)
		.author {
			font-weight: 700;
			&:before {
				content: '';
				display: inline-block;
				vertical-align: middle;
				width: 20px;
				height: 1px;
				background: #27a8e0; } } }
	.img-100 {
		width: 100%; }
	ul, ol, p, h1, h2, h3, h4, h5, h6, img, figure {
		margin-bottom: 15px;
		line-height: inherit;
		font-weight: inherit;
		@include media-breakpoint-down(xs) {
			margin-bottom: 10px; }
		// &:last-child
 }		// 	margin-bottom: 0
	figure {
		margin: 0 auto 15px;
		text-align: center;
		@include media-breakpoint-down(xs) {
			margin-bottom: 10px; }
		img {
			margin-bottom: 5px; }
		figcaption {
			// background: darken($white,5%)
			padding: 5px 0;
			text-align: center;
			@include fs(13rem);
			font-weight: 300;
			// font-style: italic
 } }			// color: darken($white,60%)
	ul {
		padding-left: 20px;
		list-style-type: disc;
		&.dot {
			list-style-type: none;
			> li {
				@include pos(rel,null);
				&:before {
					content: '';
					display: block;
					width: 8px;
					height: 8px;
					@include bor-rad(50px);
					background: darken($white,30%);
					@include pos(abs, 7px null null -20px); } } }
		&.dot-green {
			list-style-type: none;
			> li {
				@include pos(rel,null);
				&:before {
					content: '';
					display: block;
					width: 6px;
					height: 6px;
					@include bor-rad(50px);
					background: $lightgreen;
					@include pos(abs, 8px null null -20px); } } }
		&.note {
			list-style-type: none;
			padding-left: 0;
			> li {
				@include pos(rel,null);
				padding-left: 30px;
				.star {
					@include pos(abs, 0 null null 0); } } }
		&.icon {
			list-style-type: none;
			padding-left: 0;
			> li {
				padding-left: 30px;
				@include pos(rel,null);
				.material-icons {
					@include fs(18rem);
					@include pos(abs, 2px null null 0); } } }
		&.flower {
			list-style-type: none;
			padding-left: 50px;
			> li {
				@include pos(rel,null);
				margin-bottom: 40px;
				&:before {
					content: '';
					display: block;
					width: 30px;
					height: 28px;
					@include pos(abs, -4px null null -50px);
					background: url(../img/flower.png); } } }
		&.number {
			list-style-type: none;
			padding-left: 0;
			> li {
				@include pos(rel,null);
				margin-bottom: 20px;
				line-height: 1.2;
				@include flexbox;
				align-items: center;
				align-content: center;
				.number {
					@include fs(24rem);
					@include bor-rad(20px);
					background: #58a4e0;
					font-weight: bold;
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: center;
					width: 50px;
					height: 50px;
					color: $white;
					border: 2px solid $white;
					box-shadow: 2px 2px 1px color-rgba($black,0.1); }
				// +pos(abs, -15px null null -60px)
				//argin-right: 10px
				p {
					width: calc(100% - 60px); } } } }

	ol {
		list-style-type: decimal;
		padding-left: 20px;
		> li {
			padding-left: 10px; } }

	.alphabet-title {
		@include fs(18rem);
		font-weight: 700;
		text-transform: uppercase;
		color: $main;
		@include pos(rel,null);
		padding-left: 30px;
		.alphabet {
			@include pos(abs, 0 null null 0);
			color: $white;
			font-weight: 700;
			width: 24px;
			height: 24px;
			@include bor-rad(5px);
			background: $green;
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center; } }

	h6 {
		@include fs(16rem);
		line-height: 1.3;
		margin-bottom: 10px;
		@include media-breakpoint-down(sm); }
	h5 {
		@include fs(18rem);
		line-height: 1.3;
		margin-bottom: 10px; }
	h4 {
		@include fs(20rem);
		line-height: 1.3;
		margin-bottom: 10px;
		@include media-breakpoint-down(sm) {
			@include fs(16rem); } }
	h3 {
		@include fs(24rem);
		line-height: 1.3;
		margin-bottom: 10px;
		@include media-breakpoint-down(xs) {
			@include fs(18rem); } }
	h2 {
		@include fs(28rem); }
	strong {
		font-weight: 600; }
	.material-icons {
		vertical-align: middle; } }
.text-black {
	color: $main !important; }
.text-green {
	color: $green; }
.text-lgreen {
	color: $lightgreen; }
.text-orange {
	color: $orange; }
.text-red {
	color: $red !important; }
.text-yellow {
	color: $yellow; }
.text-blue {
	color: $blue !important; }
.text-bluegrey {
	color: $bluegrey !important; }
.text-silver {
	color: darken($white,40%); }


.row {
	// margin: 0px -10px
	&.row-s {
		margin: 0px -5px;
		> [class*="col-"] {
			padding: 0px 5px; } }
	&.row-m {
		margin: 0px -10px;
		> [class*="col-"] {
			padding: 0px 10px; } }
	&.no-gutters {
		margin: 0px !important;
		> [class*="col-"] {
			padding: 0px !important; } } }
.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12, .col, .col-sm-1, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12, .col-sm, .col-md-1, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-10, .col-md-11, .col-md-12, .col-md, .col-lg-1, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg, .col-xl-1, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl {
	@include transition(all,0.3s);
	// +pos(static,null)
 }	// padding: 0px 10px
.col-12 {
	width: 100%; }

a {
	color: inherit;
	@include transition(all,0.3s);
	&:hover {
		color: inherit;
		text-decoration: none; } }

* {
	line-height: 1.5;
	// +transition(all,0.3s)
	&:hover, &:active, &:focus {
		outline: none; }
	&:before, &:after {
		font-family: 'fontawesome'; } }
.lnr {
	font-family: 'linearicons-free';
	&:before, &:after {
		font-family: 'linearicons-free'; } }
.material-icons {
	line-height: 1; }
button {
	background: none;
	padding: 0;
	margin: 0;
	border: none;
	&:focus {
		border: none;
		outline: none; } }

table {
	width: 100%; }

img, iframe {
	vertical-align: middle; }

.text-xs {
	@include fz(.8rem); }
.text-sm {
	@include fz(.9rem); }
.text-md {
	@include fz(1.2rem); }
.text-lg {
	@include fz(1.4rem); }
.text-xl {
	@include fz(1.8rem); }
.text-xxl {
	@include fz(2.2rem); }
.text-shadown {
	@include text-shadow(#{1px 1px 0 #fdfdfd, 2px 2px 0 rgba(81, 81, 81, 0.5), 0 0 1px #fdfdfd}); }

.text-bold {
	font-weight: bold !important; }
.text-400 {
	@include fw(400); }
.text-medium {
	font-weight: 500 !important; }
.text-semibold {
	font-weight: 600 !important; }
.text-bold {
	font-weight: 700 !important; }
.text-extrabold {
	font-weight: 800 !important; }
