.picture-nav {
	margin-bottom: 30px;
	ul {
		@include flexbox;
		align-items: flex-end;
		align-content: flex-end;
		justify-content: center;
		background: url;
		min-width: 632px;
		margin: 0 auto;
		background: url(../img/nav.png) center bottom no-repeat;
		// border-bottom: 1px solid darken($white,10% )
		@include media-breakpoint-down(md) {
			min-width: 0; }
		li {
			+ li {
				margin-left: 10px;
				@include media-breakpoint-down(md) {
					margin-left: 5px; } }
			a {
				display: block;
				padding: 8px 20px;
				@include fs(16rem);
				@include bor-rad(5px 5px 0 0);
				background: $white;
				border: 1px solid darken($white, 10%);
				// border-bottom: none
				@include media-breakpoint-down(md) {
					@include fs(13rem);
					padding: 5px 10px; }
				@include media-breakpoint-down(sm) {
					text-align: center; } }
			&:hover {
				a {
					color: $blue; } }
			&.active {
				a {
					color: $white;
					border-color: #30588e;
					background: url(../img/navac.png) center center no-repeat;
					background-size: 100% 100%; } }
			&.download {
				a {
					color: $white;
					border-color: #30588e;
					background: url(../img/navac.png) center center no-repeat;
					background-size: 100% 100%;
					&::before {
						content: '\f019';
						line-height: 1;
						margin-right: 10px; } } } } } }

.media-page {
	padding: 50px 0;

	.album-list {
		.album {
			margin-bottom: 20px;
			.img {
				display: block;
				@include pos(rel,null);
				&::before {
					content: '';
					display: block;
					background: color-rgba($blue,0.7) url(../img/view.png) center center no-repeat;
					@include pos(abs, 0 null 0 0);
					right: 0;
					z-index: 1;
					opacity: 0;
					@include transition(all,0.3s); } }
			.caption {
				.name {
					text-align: center;
					padding: 10px 0;
					a {
						&:hover {
							color: $blue; } } } }
			&:hover {
				.img {
					&::before {
						opacity: 1; } } } } }

	.video-list {
		.video {
			margin-bottom: 20px;
			.img {
				display: block;
				@include pos(rel,null);
				&::before {
					content: '';
					display: block;
					background: color-rgba($black,0.4) url(../img/viewv.png) center center no-repeat;
					@include pos(abs, 0 null 0 0);
					right: 0;
					z-index: 1;
					@include transition(all,0.3s); } }
			.caption {
				.name {
					text-align: center;
					padding: 10px 0;
					a {
						&:hover {
							color: $blue; } } } }
			&:hover {
				.img {
					&::before {
						background: color-rgba($black,0) url(../img/viewv.png) center center no-repeat; } } } } }

	.bt-load {
		&::after {
			content: '\f0d7';
			margin-left: 10px; } } }
