.product-page {
	padding: 50px 0;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	&.s2 {
		background: color-rgba(#1d428a,0.05);
		border-top: 1px solid darken($white, 10%);
		border-bottom: 1px solid darken($white, 10%);
		.product-section {
			@include media-breakpoint-down(sm) {
				.col-12 {
					order: 1;
					&:nth-child(2) {
						order: 2; } } } } }
	.view-product {
		&:after {
			content: '\e87a';
			font-family: 'linearicons-free';
			margin-left: 15px; } }

	.product-section {
		// padding: 30px 0
		@include media-breakpoint-down(sm) {
			padding: 0;
			.col-12 {
				order: 2;
				&:nth-child(2) {
					order: 1; } } } }

	.product-list {
		.product {
			margin-bottom: 30px;
			.img {
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				height: 200px;
				img {
					max-width: 100%;
					max-height: 100%;
					@include transition(all,0.5s); } }
			.name {
				@include fs(20rem);
				text-align: center;
				font-weight: 500;
				padding: 10px;
				@include media-breakpoint-down(sm) {
					@include fs(16rem);
					font-weight: 600; } }
			&:hover {
				.img {
					img {
						@include transform(scale3d(1.1,1.1,1)); } }
				.name {
					color: $lblue; } } } }

	.product-detail {
		margin: -30px 0 0; }

	.product-other {
		margin: 30px 0;
		.other-slide {
			margin: 0px -15px;
			.item {
				// text-align: center
				padding: 0px 15px;
				.product {
					// width: 230px
					height: 230px;
					background: $white;
					border: 5px solid #dedede;
					@include flexbox;
					// display: inline-flex
					align-items: center;
					align-content: center;
					justify-content: center;
					img {
						max-width: 80%;
						max-height: 80%; } } } } } }

// NEW UPDATE
.product-page {
	.product-detail {
		margin: -50px 0;
		.detail-section {
			padding: 80px 0;
			padding-bottom: 450px;
			@include media-breakpoint-down(lg) {
				padding-top: 40px;
				padding-bottom: 300px;
				.bg {
					background-size: 100% auto !important; } }
			@include media-breakpoint-down(sm) {
				padding-bottom: 200px; }
			&.no-bg {
				padding-bottom: 80px;
				@include media-breakpoint-down(lg) {
					padding-bottom: 40px; } }
			@include pos(rel,null);
			.bg {
				@include pos(abs, 0 null 0 0);
				right: 0;
				z-index: -1; }
			.pr-logo {
				@include media-breakpoint-down(lg) {
					img {
						max-width: 300px; } } }
			.pr-desciption {
				background: #efeff0;
				padding: 25px;
				@include bor-rad(5px); } } }
	.detail-guide {
		background: color-rgba(#1d428a,0.05);
		padding: 40px 0;
		border-top: 1px solid darken($white, 10%);
		.guide-img {
			margin-bottom: 30px; } }
	.product-cert {
		margin-top: 50px; } }
