

.home-banner {
	.banner-slide {
		.slick-arrow {
			@include pos(abs,null);
			top: 50%;
			@include transform(translateY(-50%));
			z-index: 5;
			@include fs(0);
			&::before {
				color: $white;
				@include fs(24rem);
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				width: 60px;
				height: 60px;
				background: color-rgba($black,0.3);
				@include bor-rad(100px); } }
		.slick-next {
			right: 30px;
			&::before {
				content: '\f105'; } }
		.slick-prev {
			left: 30px;
			&::before {
				content: '\f104'; } }

		.slick-dots {
			@include pos(abs, null null 30px null);
			left: 50%;
			@include transform(translateX(-50%));
			li {
				display: inline-block;
				width: 10px;
				height: 10px;
				@include bor-rad(100px);
				background: $white;
				cursor: pointer;
				opacity: 0.5;
				@include transition(all,0.5s);
				+ li {
					margin-left: 10px; }
				&.slick-active {
					width: 30px;
					opacity: 1; }
				button {
					display: none; } } }

		.slick-slide {
			> div {
				display: block !important;
				> .item {
					display: block !important; } } }
		.img {
			@include pos(rel,null);
			&::before {
				content: '';
				display: block;
				@include pos(abs, 0 null 0 0);
				right: 0;
				background: #b2c7ee;
				opacity: 0.25;
				z-index: 1; }
			&::after {
				content: '';
				display: block;
				@include pos(abs, 0 null 0 0);
				right: 0;
				background: #001743;
				opacity: 0.35;
				z-index: 2; } }
		img {
			width: 100%;
			@include media-breakpoint-down(sm) {
				height: 280px;
				object-fit: cover; } }
		.caption-wrap {
			@include pos(abs, 50% 0 null 0);
			@include transform(translateY(-50%));
			z-index: 3;
			color: $white;
			.caption {
				width: 600px;
				@include media-breakpoint-down(sm) {
					width: 100%; } }
			.big {
				font-family: 'Fira Sans Condensed', sans-serif;
				@include fs(51rem);
				font-weight: 700;
				line-height: 1.3;
				text-transform: uppercase; }
			.med {
				@include fs(30rem); }
			.small {
				@include fs(18rem); }
			.bn-action {
				padding-top: 20px;
				@include flexbox;
				align-items: center;
				.bt-outline {
					margin-left: 20px; } }
			@include media-breakpoint-down(lg) {
				.big {
					@include fs(32rem); }
				.med {
					@include fs(22rem); }
				.small {
					@include fs(14rem); } }
			@include media-breakpoint-down(sm) {
				.big {
					@include fs(18rem);
					line-height: 1.5; }
				.med {
					@include fs(16rem);
					line-height: 1.5; }
				.small {
					@include fs(13rem);
					line-height: 1.5; } } } } }
