.about-story {
	padding: 150px 0;
	@include pos(rel,null);
	@include media-breakpoint-down(lg) {
		padding: 100px 0; }
	@include media-breakpoint-down(sm) {
		padding: 40px 0; }
	.bg {
		@include pos(abs, 0 null 0 0);
		right: 0;
		z-index: -1;
		&::before {
			content: '';
			@include pos(abs, 0 null 0 0);
			right: 0;
			background: $black;
			opacity: 0.5; }
		&::after {
			content: '';
			@include pos(abs, 0 null 0 0);
			right: 0;
			background: linear-gradient(to right,$black,color-rgba($black,0));
			opacity: 0.6; } } }
.about-page {
	padding: 50px 0;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.vision {
		margin-bottom: 50px;
		.vision-col {
			height: 100%;
			background: #34a4ba;
			padding: 40px;
			@include media-breakpoint-down(lg) {
				padding: 25px; }
			@include media-breakpoint-down(sm) {
				padding: 20px; } }
		.col-12 {
			&:nth-child(2) {
				.vision-col {
					background: #1e85ae; } }
			&:nth-child(3) {
				.vision-col {
					background: #1b729f; } } } }
	.story {
		margin-bottom: 50px;
		.bt-read {
			color: $blue;
			cursor: pointer;
			&:after {
				content: '\e878';
				font-family: 'linearicons-free';
				margin-left: 10px;
				font-size: 80%; }
			&:hover {
				color: $white; }
			&.active {
				&:after {
					content: '\e877'; } } }
		.content {
			.quote {
				@include media-breakpoint-down(lg) {
					width: 80% !important; }
				@include media-breakpoint-down(sm) {
					width: 100% !important; } } }
		.hidden-content {
			display: none;
			// &.active
 } }			// 	display: block
	.journey {
		margin-bottom: 50px; }

	.history-wrap {
		@include pos(rel,null);
		.history-nav {
			@include pos(rel,null);
			padding: 0px 40px;
			.slick-arrow {
				@include pos(abs, 50% null null null);
				@include transform(translateY(-50%));
				@include fs(0);
				&::before {
					content: '';
					@include fs(16rem);
					width: 40px;
					height: 40px;
					@include flexbox();
					align-items: center;
					align-content: center;
					justify-content: center;
					color: $white;
					@include bor-rad(100%);
 }					// background: color-rgba($black,0.3)
				&:hover {
					&::before {
 } }						// background: $blue
				&.slick-prev {
					left: 0px;
					&::before {
						content: '\f104'; } }
				&.slick-next {
					right: 0px;
					&::before {
						content: '\f105'; } } }
			&::before {
				content: '';
				width: 100%;
				height: 40px;
				background: $lblue;
				@include bor-rad(100px);
				@include pos(abs, 50% null null 0);
				@include transform(translateY(-50%));
				z-index: -1; }
			&::after {
				content: '';
				height: 34px;
				background: $white;
				@include bor-rad(100px);
				@include pos(abs, 50% 40px null 40px);
				@include transform(translateY(-50%));
				z-index: -1; }
			.item {
				padding: 15px;
				text-align: center;
				.year {
					@include flexbox;
					display: inline-flex;
					align-items: center;
					align-content: center;
					justify-content: center;
					width: 90px;
					height: 90px;
					@include fs(16rem);
					cursor: pointer;
					@include media-breakpoint-down(lg) {
						width: 60px;
						height: 60px; }
					&:hover {
						color: $lblue; } } }
			.slick-slide {
				&.slick-current {
					.item {
						.year {
							background: $blue;
							color: $white;
							@include fs(24rem);
							font-weight: 600;
							@include bor-rad(100px);
							border: 5px solid $white;
							box-shadow: 0px 1px 22px color-rgba($black,0.2);
							@include media-breakpoint-down(lg) {
								border-width: 3px;
								@include fs(16rem); } } } } } }
		.history-content {
			.block-content {
				padding: 30px 50px;
				@include media-breakpoint-down(sm) {
					padding: 20px 30px; } } } }

	.htcl {
		> .row {
			align-items: center;
			align-content: center; }
		margin-bottom: 50px;
		@include media-breakpoint-down(sm) {
			margin-bottom: 5px; }
		.block-content {
			padding: 15px 0;
			@include media-breakpoint-down(sm) {
				padding-bottom: 0; } }
		.block-img {
			&.split {
				@include flexbox;
				img {
					width: 40%;
					+ img {
						width: 60%;
						margin-left: 10px; } } } } }

	.qt-quote {
		max-width: 1140px;
		border: 8px solid #f3f3f3;
		background: $white;
		padding: 20px 40px 10px;
		margin: 0 auto 40px; }

	.staff-vip {
		max-width: 300px;
		margin: 0 auto 50px;
		@include media-breakpoint-down(lg) {
			margin-bottom: 15px; }
		.img {
			margin-bottom: 15px;
			background: #f6f7f6;
			img {} }
		.caption {
			text-align: center;
			.name {
				@include fs(24rem);
				line-height: 1.2;
				font-weight: 700;
				color: $blue;
				@include media-breakpoint-down(lg) {
					@include fs(20rem); }
				@include media-breakpoint-down(md) {
					@include fs(16rem); } }
			.cv {
				@include fs(18rem);
				line-height: 1.2;
				color: #777777;
				@include media-breakpoint-down(lg) {
					@include fs(16rem); }
				@include media-breakpoint-down(md) {
					@include fs(14rem); }
				&::after {
					content: '';
					display: block;
					width: 100%;
					height: 16px;
					margin: 0 0 10px;
					background: url(../img/line2.png) center no-repeat; } }
			.des {
				@include fs(16rem);
				line-height: 1.3;
				@include media-breakpoint-down(md) {
					@include fs(14rem); } } } }
	.staff {
		max-width: 300px;
		margin: 0 auto 50px;
		@include media-breakpoint-down(lg) {
			margin-bottom: 15px; }
		.img {
			margin-bottom: 15px;
			background: #f6f7f6;
			img {} }
		.caption {
			text-align: center;
			.name {
				@include fs(20rem);
				line-height: 1.2;
				font-weight: 700;
				color: $main;
				@include media-breakpoint-down(lg) {
					@include fs(18rem); }
				@include media-breakpoint-down(md) {
					@include fs(16rem); } }
			.cv {
				@include fs(16rem);
				line-height: 1.2;
				color: #777777;
				@include media-breakpoint-down(lg) {
					@include fs(14rem); }
				@include media-breakpoint-down(md) {
					@include fs(13rem); }
				&::after {
					content: '';
					display: block;
					width: 100%;
					height: 16px;
					margin: 0 0 10px;
					background: url(../img/line2.png) center no-repeat; } } } } }


.location-list {
	.row {
		justify-content: center; }
	.location {
		@include pos(rel,null);
		margin-bottom: 30px;
		@include bor-rad(5px);
		overflow: hidden;
		border: 2px solid #e2e2e2;
		.img {
			display: block;
			@include pos(rel,null);
			&:before {
				content: '';
				@include pos(abs, 0 null 0 0);
				right: 0;
				background: linear-gradient(color-rgba($black,0),color-rgba($black,1));
				z-index: 1; }
			img {
				width: 100%;
				@include transition(all,0.5s); } }
		.caption {
			@include pos(abs, null 0 0 0);
			z-index: 2;
			padding: 15px 20px;
			.name {
				@include fs(16rem);
				color: $white;
				line-height: 1.3;
				font-weight: 700;
				text-transform: uppercase; }
			ul {
				li {
					@include pos(rel,null);
					@include fs(14rem);
					font-weight: normal;
					line-height: 1.3;
					color: $white;
					padding-left: 30px;
					margin-bottom: 5px;
					.icon {
						@include pos(abs, 0 null null 0);
						img {
							width: 18px; } }
					a {
						color: $white;
						&:hover {
							color: $lblue; } } } } }
		&:hover {
			.img {
				img {
						@include transform(scale3d(1.1,1.1,1)); } } }
		&.active {
			border: 2px solid $blue;
			// &::before
			// 	content: ''
			// 	display: block
			// 	width: 0
			// 	height: 0
			// 	border: 10px solid transparent
			// 	border-top-color: none
			// 	border-bottom-color: #e2e2e2
			// 	z-index: 1
			// 	+pos(abs, null null -30px 50%)
 } } }			// 	+transform(translateX(-50%))

.cert-slide-2 {
	.slick-arrow {
		@include pos(abs, 50% null null null);
		@include transform(translateY(-50%));
		@include fs(0);
		&::before {
			content: '';
			@include fs(16rem);
			width: 30px;
			height: 30px;
			@include flexbox();
			align-items: center;
			align-content: center;
			justify-content: center;
			color: $white;
			@include bor-rad(100%);
			background: color-rgba($black,0.3); }
		&:hover {
			&::before {
				background: $blue; } }
		&.slick-prev {
			left: -40px;
			@include media-breakpoint-down(sm) {
				left: -15px; }
			&::before {
				content: '\f104'; } }
		&.slick-next {
			right: -40px;
			@include media-breakpoint-down(sm) {
				right: -15px; }
			&::before {
				content: '\f105'; } } }
	.item {
		.cert {
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			height: 180px;
			@include media-breakpoint-down(sm) {
				height: 100px; }
			img {
				max-width: 100%;
				max-height: 100%;
				@include media-breakpoint-down(sm) {
					max-width: 80%;
					max-height: 80%; } } } } }
.cert-slide-3 {
	@extend .cert-slide-2;
	.item {
		.cert {
			height: 380px;
			@include media-breakpoint-down(lg) {
				height: 200px; }
			@include media-breakpoint-down(sm) {
				height: 120px; } } } }

.cert-slide-4 {
	@extend .cert-slide-2;
	.item {
		.cert {
			height: 120px;
			img {
				max-width: 80%;
				max-height: 80%; }
			@include media-breakpoint-down(lg) {
				height: 100px; }
			@include media-breakpoint-down(sm) {
				height: 80px; } } } }
