.news-page {
	padding: 50px 0;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.news-list {
		.news {
			@include media-breakpoint-down(sm) {
				margin-bottom: 10px; }
			.img {
				display: block;
				img {
					width: 100%;
					height: 330px;
					object-fit: cover; } }
			.caption {
				background: #f3f3f3;
				padding: 10px 15px;
				time {
					display: block;
					@include fs(14rem);
					color: darken($white,30);
					margin-bottom: 5px;
					@include media-breakpoint-down(sm) {
						@include fs(13rem); }
					&:before {
						content: '\e836';
						font-family: 'linearicons-free';
						margin-right: 10px; } }
				.name {
					margin-bottom: 5px;
					a {
						@include fs(22rem);
						line-height: 1.2;
						font-weight: 600;
						color: $main;
						@include textover(3);
						@include media-breakpoint-down(sm) {
							@include fs(16rem); } } }
				.des {
					@include fs(16rem);
					line-height: 1.3;
					@include textover(2);
					margin-bottom: 5px;
					@include media-breakpoint-down(sm) {
						@include fs(14rem); } }
				.link {
					color: $blue;
					&::after {
						content: '\e876';
						font-family: 'linearicons-free';
						font-size: 80%;
						margin-left: 5px; }
					&:hover {
						color: $lblue; } } }
			&.big {
				.caption {
					padding: 15px 0;
					background: none; } } } }
	.news-slide {
		overflow: hidden;
		@include pos(rel,null);
		margin-bottom: 20px;
		.slick-arrow {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			@include fs(0);
			z-index: 5;
			&::before {
				content: '';
				font-family: 'linearicons-free';
				@include fs(16rem);
				width: 30px;
				height: 80px;
				@include flexbox();
				align-items: center;
				align-content: center;
				justify-content: center;
				color: $white;
				@include bor-rad(0 5px 5px 0);
				background: color-rgba($black,0.5); }
			&:hover {
				&::before {
					background: color-rgba($blue,0.8); } }
			&.slick-prev {
				left: 0px;
				&::before {
					content: '\e879'; } }
			&.slick-next {
				right: 0px;
				&::before {
					content: '\e87a';
					@include bor-rad(5px 0 0 5px); } } }
		.slick-dots {
			text-align: center;
			li {
				display: inline-block;
				width: 6px;
				height: 6px;
				@include bor-rad(50px);
				background: #b5b5b5;
				+ li {
					margin-left: 8px; }
				&.slick-active {
					background: $blue; }
				button {
					display: none; } } }
		.slick-list {
			margin: 0px -15px;
			@include media-breakpoint-down(sm) {
				margin: 0px -5px; } }
		.item {
			padding: 0px 15px;
			@include media-breakpoint-down(sm) {
				padding: 0px 5px; } }
		.news {
			.img {
				display: block; }
			.caption {
				padding: 10px 0;
				time {
					display: block;
					@include fs(14rem);
					color: darken($white,30);
					margin-bottom: 5px;
					@include media-breakpoint-down(sm) {
						@include fs(13rem); }
					&:before {
						content: '\e836';
						font-family: 'linearicons-free';
						margin-right: 10px; } }
				.name {
					a {
						color: $black;
						@include textover(2);
						@include media-breakpoint-down(sm) {
							@include fs(14rem);
							font-weight: 500;
							line-height: 1.3; }
						&:hover {
							color: $blue; } } } } } }

	.news-list2 {
		padding-top: 30px;
		border-top: 3px solid $blue;
		.news {
			@include flexbox;
			flex-wrap: wrap;
			margin-bottom: 30px;
			.img {
				display: block;
				width: 45%;
				@include media-breakpoint-down(sm) {
					width: 80px; } }
			.caption {
				width: 55%;
				padding-left: 30px;
				@include media-breakpoint-down(sm) {
					width: calc(100% - 80px);
					padding-left: 10px; }
				time {
					display: block;
					@include fs(14rem);
					color: darken($white,30);
					margin-bottom: 5px;
					@include media-breakpoint-down(sm) {
						@include fs(13rem); }
					&:before {
						content: '\e836';
						font-family: 'linearicons-free';
						margin-right: 10px; } }
				.name {
					margin-bottom: 5px;
					a {
						color: $black;
						@include fs(22rem);
						line-height: 1.2;
						font-weight: 600;
						@include textover(3);
						@include media-breakpoint-down(sm) {
							@include fs(16rem);
							@include textover(2); }
						&:hover {
							color: $blue; } } }
				.des {
					@include fs(16rem);
					line-height: 1.3;
					@include textover(6);
					@include media-breakpoint-down(sm) {
						@include fs(14rem);
						@include textover(2); } } } } } }

.qc {
	.item {
		+ .item {
			margin-top: 10px; }
		.img {
			display: block;
			img {
				width: 100%; } } } }

.news-detail {
	.news-name {
		@include fs(36rem);
		line-height: 1.2;
		font-weight: 600;
		margin-bottom: 10px;
		@include media-breakpoint-down(lg) {
			@include fs(28rem); }
		@include media-breakpoint-down(sm) {
			@include fs(16rem); } }
	.share-wrap {
		@include flexbox;
		align-items: center;
		align-content: center;
		margin-bottom: 15px;
		time {
			display: block;
			@include fs(14rem);
			color: darken($white,30);
			margin-left: 20px;
			&:before {
				content: '\e836';
				font-family: 'linearicons-free';
				margin-right: 10px; } } }
	.en-click {
		display: block;
		padding: 15px 0;
		color: $lblue; }
	.news-content {
		padding-bottom: 30px;
		border-bottom: 1px dashed darken($white, 20%);
		margin-bottom: 20px; } }

.s-title {
	font-weight: 600;
	@include fs(20rem);
	text-transform: uppercase;
	margin-bottom: 10px; }
.news-other {}

.news-list3 {
	a {
		display: block;
		@include fs(16rem);
		color: #222222;
		@include pos(rel,null);
		padding-left: 20px;
		@include media-breakpoint-down(sm) {
			@include fs(14rem); }
		span {
			color: #a1a1a1; }
		&::before {
			content: '';
			width: 6px;
			height: 6px;
			background: darken($white, 40%);
			@include pos(abs, 7px null null 0); }
		&:hover {
			color: $lblue;
			&::before {
				background: $lblue; } } } }

.evo-page {
	padding: 50px 0; }
.evo-feature {
	max-width: 900px;
	margin: 0 auto 20px;
	.evo {
		margin-bottom: 20px;
		@include flexbox;
		justify-content: center; } }
.evo-quote {
	border: 8px solid #f3f3f3;
	padding: 30px 0 15px;
	.content {
		text-align: center;
		max-width: 730px;
		margin: 0 auto;
		@include pos(rel,null);
		@include fs(18rem);
		&::before {
			content: '';
			display: block;
			width: 78px;
			height: 66px;
			background: url(../img/ql.png) center center no-repeat;
			@include pos(abs, -20px null null -90px);
			z-index: 1; }
		&::after {
			content: '';
			display: block;
			width: 48px;
			height: 41px;
			background: url(../img/qr.png) center center no-repeat;
			@include pos(abs, null -60px -20px null);
			z-index: 1; } } }
.evo-img {
	margin: 50px 0; }
.evo-list {
	max-width: 920px;
	margin: 0 auto;
	.evo-item {
		margin-bottom: 30px;
		.img {
			display: block;
			img {
				width: 100%; } }
		.caption {
			padding: 10px;
			.name {
				display: block;
				@include textover(2);
				text-align: center;
				&:after {
					content: '';
					display: block;
					width: 40px;
					height: 2px;
					background: $blue;
					margin: 10px auto 0; } } } } }

.news-detail-page {
	.news-list, .news-slide {
		display: none; } }
