.home-stitle {
	@include fs(21rem);
	font-weight: 500;
	color: darken($white, 50%);
	text-transform: uppercase;
	@include media-breakpoint-down(lg) {
		@include fs(16rem); }
	@include media-breakpoint-down(sm) {
		@include fs(14rem); } }

.home-title {
	font-family: 'Fira Sans Condensed', sans-serif;
	@include fs(30rem);
	font-weight: 700;
	line-height: 1.2;
	color: $blue;
	text-transform: uppercase;
	margin-bottom: 15px;
	> * {
		font-family: 'Fira Sans Condensed', sans-serif;
		font-weight: 700; }
	@include media-breakpoint-down(lg) {
		@include fs(22rem); }
	@include media-breakpoint-down(sm) {
		@include fs(18rem); }
	&::after {
		content: '';
		display: block;
		width: 181px;
		height: 16px;
		margin-top: 10px;
		background: url(../img/line.png) center no-repeat;
		@include media-breakpoint-down(lg) {
			height: 10px;
			background-size: auto 100%;
			background-position: left center; } }
	&.sm {
		@include fs(22rem);
		@include media-breakpoint-down(lg) {
			@include fs(18rem); } }
	&.text-right {
		&::after {
			width: 100%;
			background-position: left top;
			@include transform(scale(-1,1)); } }
	&.text-center {
		&::after {
			content: '';
			width: 100%;
			background: url(../img/line2.png) center no-repeat;
			@include media-breakpoint-down(lg) {
				height: 10px;
				background-size: auto 100%;
				background-position: center; } } } }

.home-section {
	padding: 40px 0;
	@include pos(rel,null);
	.bg {
		@include pos(abs, 0 null 0 0);
		right: 0;
		z-index: -1;
		filter: brightness(60%);
		background-size: cover !important; } }

.home-about {
	@extend .home-section;
	.video {
		display: block;
		@include pos(rel,null);
		&:before {
			content: '';
			display: block;
			background: url(../img/play.png) center no-repeat;
			@include pos(abs, 0 null 0 0);
			right: 0;
			z-index: 1; }
		img {
			width: 100%;
			filter: brightness(70%); }
		&:hover {
			&::before {
				display: none; } } } }

.home-cate {
	padding-bottom: 40px;
	@include pos(rel,null);
	@include media-breakpoint-down(sm) {
		.col-img {
			order: 1; }
		.col-content {
			order: 2; } }
	&:nth-child(even) {
		.col-lg-5 {
			@include flexbox;
			justify-content: flex-end;
			text-align: right; }
		.home-title {
			&::after {
				width: 100%;
				background-position: left top;
				transform: scaleX(-1); } } }
	.block-img {
		img {
			width: 100%;
			height: 370px;
			object-fit: cover;
			@include media-breakpoint-down(lg) {
				height: 300px; }
			@include media-breakpoint-down(sm) {
				height: 200px;
				margin: 15px 0; } } }
	.block-content {
		max-width: 460px;
		padding: 0px 30px; }
	.bt-primary {
		display: inline-block;
		width: 300px;
		&:after {
			content: '\e87a';
			font-weight: normal;
			vertical-align: top;
			font-family: 'Linearicons-Free';
			margin-left: 10px; } } }

.home-sale {
	background: #1f4284;
	.block-img {
		padding: 25px 0;
		@include media-breakpoint-down(lg) {
			text-align: center; } }
	.block-content {
		color: $white;
		@include media-breakpoint-down(lg) {
			text-align: center; }
		.title {
			@include fs(26rem);
			font-family: 'Fira Sans Condensed', sans-serif;
			font-weight: 600;
			text-transform: uppercase;
			margin-bottom: 15px;
			@include media-breakpoint-down(lg) {
				@include fs(24rem); }
			@include media-breakpoint-down(sm) {
				@include fs(18rem); } }
		.s-title {
			@include fs(26rem);
			// font-weight: normal
			font-family: 'Fira Sans Condensed', sans-serif;
			margin-bottom: 15px;
			@include media-breakpoint-down(lg) {
				@include fs(16rem); }
			@include media-breakpoint-down(sm) {
				@include fs(14rem); } }
		.btn-actions {
			@include flexbox;
			align-items: center;
			@include media-breakpoint-down(lg) {
				justify-content: center;
				margin-bottom: 30px; }
			@include media-breakpoint-down(sm) {
				flex-direction: column; }
			.bt-primary {
				color: #163572;
				background: $white;
				@include media-breakpoint-down(sm) {
					width: 100%; }
				+ .bt-primary {
					margin-left: 30px;
					@include media-breakpoint-down(lg) {
						margin-left: 15px; }
					@include media-breakpoint-down(sm) {
						margin-left: 0;
						margin-top: 10px; } }
				&::after {
					content: '\e876';
					font-family: 'linearicons-free';
					font-size: 60%;
					margin-left: 10px; }
				&:hover {
					color: $white;
					background: $lblue; } } } } }

.home-special {
	@extend .home-section;
	.block-content {
		max-width: 1060px;
		margin: 0 auto; }
	.features {
		.col-12 {
			@include media-breakpoint-down(sm) {
				flex: 0 0 50%;
				max-width: 50%; } }
		.feature {
			text-align: center;
			.caption {
				color: $white;
				height: 80px;
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				flex-direction: column;
				@include media-breakpoint-down(lg) {
					height: 60px; }
				.number {
					@include fs(33rem);
					font-weight: 600;
					line-height: 1.2;
					@include media-breakpoint-down(lg) {
						@include fs(20rem); }
					strong {
						@include fs(48rem);
						font-weight: 600;
						@include media-breakpoint-down(lg) {
							@include fs(24rem); } }
					span {
						@include fs(24rem);
						font-weight: 500;
						@include media-breakpoint-down(lg) {
							@include fs(14rem); } } }
				.desc {
 } } } } }					// +fs(16rem)

.home-news {
	@extend .home-section;
	.news-list {
		.news {
			margin-bottom: 15px;
			.img {
				display: block;
				margin-bottom: 10px;
				img {
					width: 100%; } }
			.caption {
				time {
					display: block;
					@include fs(14rem);
					color: darken($white,30);
					margin-bottom: 5px;
					@include media-breakpoint-down(sm) {
						@include fs(12rem); }
					&:before {
						content: '\e836';
						font-family: 'linearicons-free';
						margin-right: 10px; } }
				.name {
					margin-bottom: 5px;
					a {
						display: block;
						// +fs(16rem)
						line-height: 1.3;
						// font-weight: 600
						color: $main;
						overflow: hidden;
						display: -webkit-box;
						-webkit-line-clamp: 2;
						-webkit-box-orient: vertical;
						@include media-breakpoint-down(sm) {
							@include fs(14rem); }
						&:hover {
							color: $lblue; } } }
				.des {
					// +fs(16rem)
					overflow: hidden;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					margin-bottom: 5px;
					@include media-breakpoint-down(sm) {
						@include fs(13rem); } }
				.link {
					@include fs(13rem);
					color: $blue;
					&::after {
						content: '\e876';
						font-family: 'linearicons-free';
						font-size: 80%;
						margin-left: 5px; }
					&:hover {
						color: $lblue; } } }
			&.big {
				.img {
					margin-bottom: 20px; }
				.caption {
					.name {
						a {
							@include fs(20rem);
							font-weight: 600;
							@include media-breakpoint-down(sm) {
								@include fs(16rem); } } } } } } } }

.home-brand {
	@extend .home-section;
	.bg {
		filter: brightness(100%); }
	.block-content {
		max-width: 960px;
		margin: 0 auto 30px; }
	.brands {
		@include flexbox;
		justify-content: center;
		align-items: center;
		.brand {
			@include flexbox;
			width: 200px;
			height: 150px;
			align-items: center;
			align-content: center;
			justify-content: center;
			background: $white;
			@include media-breakpoint-down(lg) {
				height: 100px; }
			@include media-breakpoint-down(sm) {
				height: 80px; }
			+ .brand {
				margin-left: 30px; }
			img {
				max-width: 80%;
				max-height: 80%; } } } }

.home-cert {
	@extend .home-section;
	.cert-slide {
		@include pos(rel,null);
		margin: 0px -15px;
		@include media-breakpoint-down(sm) {
			margin: 0px 10px; }
		.slick-arrow {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			@include fs(0);
			z-index: 5;
			&::before {
				content: '';
				@include fs(16rem);
				width: 30px;
				height: 30px;
				@include flexbox();
				align-items: center;
				align-content: center;
				justify-content: center;
				color: $white;
				@include bor-rad(100%);
				background: color-rgba($black,0.3); }
			&:hover {
				&::before {
					background: $blue; } }
			&.slick-prev {
				left: -30px;
				@include media-breakpoint-down(sm) {
					left: -15px; }
				&::before {
					content: '\f104'; } }
			&.slick-next {
				right: -30px;
				@include media-breakpoint-down(sm) {
					right: -15px; }
				&::before {
					content: '\f105'; } } }
		.item {
			padding: 0px 15px;
			@include media-breakpoint-down(sm) {
				padding: 0px 10px; }
			.cert {
				@include flexbox;
				height: 165px;
				justify-content: center;
				align-items: center;
				align-content: center;
				@include bor-rad(5px);
				background: $white;
				border: 1px solid darken($white,20%);
				@include media-breakpoint-down(lg) {
					height: 100px; }
				img {
					max-width: 80%;
					max-height: 80%; } } } } }

.home-career {
	@extend .home-section;
	margin-bottom: 60px;
	@include media-breakpoint-down(sm) {
		.col-12 {
			&:nth-child(1) {
				order: 2; }
			&:nth-child(2) {
				order: 1;
				margin-bottom: 15px; } } } }
