footer {
	.col-12 {
		@include media-breakpoint-down(lg) {
			margin-bottom: 30px; } }

	.footer-top {
		background: url(../img/ft.jpg) center center / cover no-repeat;
		padding: 30px 0; }
	.footer-title {
		@include fs(18rem);
		color: $white;
		// font-family: 'cooper'
		font-weight: 700;
		text-transform: uppercase;
		margin-bottom: 30px;
		@include media-breakpoint-down(lg) {
			@include fs(16rem); }
		&::after {
			content: '';
			display: block;
			width: 120px;
			height: 1px;
			background: $white;
			margin-top: 10px; } }
	.contact-info {
		ul {
			li {
				@include pos(rel,null);
				font-weight: normal;
				line-height: 1.3;
				color: $white;
				margin-bottom: 10px;
				// padding-left: 45px
				// +flexbox
				// align-items: center
				// align-content: center
				// min-height: 50px
				@include media-breakpoint-down(lg) {
					@include fs(13rem); }
				.icon {
					display: none;
					@include pos(abs, 50% null null 0);
					@include transform(translateY(-50%)); }
				a {
					color: $white;
					&:hover {
						color: $lblue; } } } } }
	.footer-link {
		ul {
			li {
				padding-left: 20px;
				@include pos(rel,null);
				margin-bottom: 7px;
				@include media-breakpoint-down(lg) {
					@include fs(13rem); }
				&::before {
					content: '';
					width: 8px;
					height: 8px;
					background: color-rgba($white,0.8);
					@include bor-rad(100px);
					@include pos(abs, 7px null null 0);
					@include media-breakpoint-down(lg) {
						width: 6px;
						height: 6px;
						top: 5px; } }
				a {
					color: $white;
					&:hover {
						color: $lblue; } } } } }
	.social {
		@include flexbox();
		align-items: center;
		align-content: center;
		margin-bottom: 8px;
		a {
			+ a {
				margin-left: 10px; }
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			width: 30px;
			height: 30px;
			@include bor-rad(100%);
			background: $white;
			color: $blue;
			opacity: 0.8;
			&:hover {
				opacity: 1; } } } }
