.career-page {
	padding: 50px 0;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; } }

.job-list {
	.job-item {
		background: $white;
		border: 1px solid darken($white, 10%);
		border-left-width: 3px;
		margin-bottom: 30px;
		@include media-breakpoint-down(sm) {
			margin-bottom: 15px; }
		&.active {
			border-left-color: $blue; }
		.detail {
			padding: 0px 30px;
			@include media-breakpoint-down(sm) {
				padding: 0px 15px; }
			.name {
				@include fs(22rem);
				line-height: 1.3;
				color: $blue;
				font-weight: 600;
				text-transform: uppercase;
				padding: 10px 0;
				margin-top: 5px;
				border-bottom: 1px solid darken($white,10%);
				@include media-breakpoint-down(sm) {
					@include fs(16rem); } }
			.des {
				padding: 15px 0;
				.content {
					max-height: 150px;
					overflow: hidden;
					margin-bottom: 10px;
					@include pos(rel,null);
					&::after {
						content: '';
						display: block;
						width: 100%;
						height: 50px;
						background: linear-gradient(color-rgba($white,0),color-rgba($white,1));
						@include pos(abs, null 0 0 0);
						z-index: 1; }
					&.active {
						max-height: unset;
						&::after {
							display: none; } } } }

			.viewdetail {
				font-weight: 600;
				color: $blue;
				@include media-breakpoint-down(sm) {
					@include fs(13rem); }
				&::before {
					content: '+';
					margin-right: 10px;
					font-family: 'Baloo Paaji 2', cursive; }
				&:hover {
					color: $lblue; }
				&.active {
					&::before {
						content: '-'; } } } }
		.info {
			height: 100%;
			border-left: 1px solid darken($white, 10%);
			background: #f6f7f9;
			padding: 30px 40px;
			@include media-breakpoint-down(sm) {
				padding: 15px; }
			.title {
				@include fs(24rem);
				font-weight: 600;
				text-transform: uppercase;
				margin-bottom: 10px;
				@include media-breakpoint-down(sm) {
					@include fs(16rem); } }
			ul {
				margin-bottom: 10px;
				li {
					@include fs(16rem);
					@include media-breakpoint-down(sm) {
						@include fs(14rem); } } }
			.bt-primary {
				&:after {
					content: '\f105';
					margin-left: 10px; } } } } }

.evo-relative {
	@include flexbox;
	margin-top: 30px;
	@include media-breakpoint-down(xs) {
		display: block; }
	.relative-item {
		flex: 1;
		height: 300px;
		@include transition(all,0.5s);
		@include pos(rel,null);
		@include media-breakpoint-down(xs) {
			display: block;
			flex: unset;
			width: 100%;
			height: 200px; }
		.img {
			@include pos(abs, 0 null 0 0);
			right: 0;
			z-index: 1;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				filter: brightness(80%);
				@include transition(all,0.5s); } }
		.caption {
			@include pos(abs, 0 null 0 0);
			right: 0;
			z-index: 2;
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			text-align: center;
			padding: 15px;
			.name {
				@include fs(22rem);
				line-height: 1.3;
				font-weight: 500;
				text-transform: uppercase;
				color: $white; } }
		&:hover {
			@include media-breakpoint-up(sm) {
				flex: 2;
				.img {
					img {
						filter: brightness(50%); } } } } } }
