body {
	padding-top: 60px;
	@include media-breakpoint-down(lg) {
		padding-top: 50px; } }

header {
	background: $blue;
	@include pos(fix, 0 0 null 0);
	z-index: 1000;
	.header-main {}
	.header-wrap {
		@include flexbox;
		justify-content: space-between;
		align-items: center;
		align-content: center; }
	.col-right {
		@include flexbox;
		justify-content: flex-end;
		align-items: center;
		align-content: center; }
	.sitelogo {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		height: 60px;
		@include media-breakpoint-down(lg) {
			height: 50px; }
		a {
			display: block;
			img {
				height: 40px;
				@include media-breakpoint-down(lg) {
					height: 30px; } } } }
	.btn-showmenu {
		display: none;
		width: 20px;
		height: 15px;
		@include pos(rel,null);
		&::before {
			content: '';
			display: block;
			width: 100%;
			height: 1px;
			background: $white;
			@include pos(abs, 0 0 null 0);
			@include transition(all,0.5s); }
		&::after {
			content: '';
			display: block;
			width: 100%;
			height: 1px;
			background: $white;
			@include pos(abs, null 0 0 0);
			@include transition(all,0.5s); }
		span {
			display: block;
			width: 100%;
			height: 1px;
			background: $white;
			@include transition(all,0.5s); }
		&.active {
			&::before {
				top: 50%;
				@include transform(translateY(-50%)rotateZ(45deg)); }
			&::after {
				bottom: 50%;
				@include transform(translateY(50%)rotateZ(-45deg)); }
			span {
				opacity: 0;
				visibility: hidden; } }

		@include media-breakpoint-down(lg) {
			@include flexbox;
			align-items: center;
			align-content: center; } }

	.menu {
		@include media-breakpoint-down(lg) {
			@include pos(fix, 50px 0 0 0);
			background: $white;
			z-index: 1000;
			display: none; }
		.menu-link {
			@include flexbox;
			align-items: center;
			align-content: center;
			@include media-breakpoint-down(lg) {
				display: block;
				padding: 0px 40px; }
			> li {
				@include flexbox;
				align-items: center;
				align-content: center;
				height: 60px;
				@include media-breakpoint-down(lg) {
					height: unset;
					display: block;
					border-bottom: 1px solid darken($white, 10%);
					@include pos(rel,null); }
				+ li {
					> a {
						&:before {
							content: '';
							display: block;
							width: 0;
							height: 12px;
							border-left: 1px dotted color-rgba($white,0.5);
							@include pos(abs, 50% null null 0);
							@include transform(translateY(-50%)); } } }
				> a {
					display: block;
					width: 100%;
					color: $white;
					@include fs(16rem);
					font-weight: 500;
					text-transform: uppercase;
					padding: 0px 15px;
					@include pos(rel,null);
					@include media-breakpoint-down(lg) {
						color: $main;
						padding: 10px 0; } }
				.btn-showsub {
					display: none;
					@include media-breakpoint-down(lg) {
						display: block;
						@include pos(abs, 6px 0 null null);
						z-index: 100;
						@include transition(all,0.3s);
						&::before {
							content: '\f107';
							display: block;
							width: 30px;
							line-height: 30px;
							text-align: center;
							@include fs(18rem); }
						&.active {
							@include transform(scale(1,-1)); } } }
				.sub {
					display: none;
					@include pos(fix, 60px 0 null 0);
					z-index: 1100;
					background: $white;
					box-shadow: 0 5px 15px color-rgba($black,0.45);
					@include media-breakpoint-down(lg) {
						@include pos(static,null);
						box-shadow: none; }
					.sub-container {
						@extend .container;
						@include flexbox;
						@include media-breakpoint-down(lg) {
							padding: 0; }
						ul {
							width: 330px;
							padding: 10px 0;
							background: #f3f3f3;
							@include pos(rel,null);
							@include media-breakpoint-down(lg) {
								width: 100%;
								background: $white;
								border-top: 1px solid darken($white, 10%);
								padding: 0; }
							&:before {
								content: '';
								display: block;
								width: 1000px;
								background: #f3f3f3;
								@include pos(abs, 0 100% 0 null);
								@include media-breakpoint-down(lg) {
									display: none; } }
							li {
								@include pos(rel,null);
								+ li {
									@include media-breakpoint-down(lg) {
										border-top: 1px dashed darken($white, 10%); } }
								a {
									display: block;
									padding: 16px 15px;
									@include fs(16rem);
									line-height: 20px;
									font-weight: 500;
									@include media-breakpoint-down(lg) {
										@include fs(16rem);
										padding: 10px 0;
										color: darken($white, 50%); } }
								&:hover {
									background: $white;
									&:before {
										content: '';
										display: block;
										width: 1000px;
										background: #fff;
										@include pos(abs, 0 100% 0 null);
										@include media-breakpoint-down(lg) {
											display: none; } } }
								&.active {
									@include media-breakpoint-down(lg) {
										a {
											color: $red;
											font-weight: 600; } } } } }
						.sub-img {
							width: calc(100% - 330px);
							@include media-breakpoint-down(lg) {
								display: none; } } } }
				&:hover {
					@include media-breakpoint-up(xl) {
						background: $lblue;
						.sub {
							display: block; } } }
				&.active {
					@include media-breakpoint-down(lg) {
						> a {
							color: $blue;
							font-weight: 600; } } } } } }

	.language {
		@include flexbox;
		align-items: center;
		align-content: center;
		@include media-breakpoint-down(lg) {
			padding: 15px 40px; }
		a {
			@include fs(13rem);
			font-weight: 500;
			color: #5e76a5;
			+ a {
				&:before {
					content: '/';
					color: #5e76a5;
					margin: 0px 5px; } }
			&.active {
				color: $white;
				@include media-breakpoint-down(lg) {
					color: $blue;
					font-weight: 700; } } } }

	.search {
		margin-left: 15px;
		@include pos(rel,null);
		@include media-breakpoint-down(lg) {
			margin-left: 0; }
		.search-toggle {
			color: $white;
			cursor: pointer;
			@include fs(16rem);
			@include media-breakpoint-down(lg) {
				@include flexbox;
				width: 20px;
				height: 16px;
				align-items: center;
				align-content: center;
				justify-content: center; } }
		.search-wrap {
			display: none;
			@include pos(abs, 42px 0 null null);
			z-index: 500;
			.searchbox {
				input.searchinput {
					background: $main;
					color: $white;
					@include fs(16rem);
					line-height: 1;
					padding: 8px 20px;
					border: none; }
				button.searchbutton {
					@include fs(0);
					color: $white;
					@include pos(abs, 10px 10px null null);
					opacity: 0.5;
					&::before {
						content: '\f002';
						@include fs(16rem); }
					&:hover {
						opacity: 1; } } } } } }



