body.searchresults {
	.home-banner {
		display: none; } }
.search-page {
	margin: 40px auto;
	max-width: 800px;
	.search-page-top {
		@include flexbox;
		flex-direction: column;
		align-content: center;
		justify-content: center;
		.page-header {
			display: none; }
		.searchresultsummary {
			color: darken($white, 60%);
			margin-bottom: 5px; }
		.searchcontrols {
			.form-inline {
				@include pos(rel,null);
				.form-group {
					width: 100%;
					.form-control {
						width: 100%;
						padding-right: 100px; } }
				.seachpage-btn {
					@include pos(abs, 2px 2px null null);
					z-index: 5;
					padding: 4px 15px;
					background: $blue;
					color: $white;
					font-weight: 500;
					&:focus {
						box-shadow: none; } } } } }
	.text-danger {
		@extend .alert;
		@extend .alert-danger;
		padding: 0.375rem 0.75rem;
		&::before {
			content: '\f071'; } }
	.modulepager {
		display: none; } }
.searchresultlist {
	margin-top: 15px;
	@include bor-rad(5px);
	border: 1px solid darken($white, 5%);
	box-shadow: 0px 3px 5px color-rgba($black,0.05);
	.searchresult {
		background: $white;
		padding: 15px 20px;
		+ .searchresult {
			border-top: 1px solid darken($white, 5%); }
		a {
			display: block;
			font-weight: 600;
			color: $blue;
			@include textover(2);
			&:hover {
				color: $red;
				background: color-rgba($black,0.02); } }
		.searchresultdesc {
			@include textover(4); } }
	h3 {}
	hr {
		display: none; }
	+ .modulepager {
		display: block;
		margin: 40px 0; } }
